body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    background: #eeeeee;
}

.todo-list-template {
    background: white;
    width:500px;
    margin:0 auto;
    margin-top:4rem;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.title {
    background: #335d8d;
    color:white;
    text-align: center;
    font-size:2rem;
    padding:30px 0;
}

.form-area {
    padding:1rem;
    border-bottom:1px solid #cccccc;
}

.todo-list-area {
    min-height:5rem;
}
.form {display: flex;}

.form input {
    border:none;
    border-bottom:1px solid #335d8d;
    flex:1 1;
    font-size: 1.25rem;
    outline: none;
}

.create-button {
    background: #335d8d;
    color:white;
    padding:0.5rem 1.5rem;
    border-radius: 3px;
    font-weight:600;
    cursor: pointer;
}

.create-button:hover {
    background: #20c8df;
}
.todo-item {
    padding:1rem;
    display: flex;
    align-items: center;
    cursor: pointer;
    -webkit-transition: all 0.15s;
    transition: all 0.15s;
}

.todo-item:hover {
    background: #e3fafc;
}

.todo-item:hover .remove {
    opacity: 1;
}

.todo-item + .todo-item {
    border-top: 1px solid #cccccc;
}

.remove {
    margin-right:1rem;
    color:red;
    font-weight: 600;
    opacity: 0;
}

.todo-text {
    flex: 1 1;
    word-break: break-all;
}

.checked {
    text-decoration: line-through;
    color: #adb5bd;
  }

  .check-mark {
    font-size: 1.5rem;
    line-height: 1rem;
    margin-left: 1rem;
    color: #3bc9db;
    font-weight: 800;
  }
