body {
    background: #eeeeee;
}

.todo-list-template {
    background: white;
    width:500px;
    margin:0 auto;
    margin-top:4rem;
    box-shadow: 0 3px 6px rgba(0,0,0,0.16), 0 3px 6px rgba(0,0,0,0.23);
}

.title {
    background: #335d8d;
    color:white;
    text-align: center;
    font-size:2rem;
    padding:30px 0;
}

.form-area {
    padding:1rem;
    border-bottom:1px solid #cccccc;
}

.todo-list-area {
    min-height:5rem;
}