.form {display: flex;}

.form input {
    border:none;
    border-bottom:1px solid #335d8d;
    flex:1;
    font-size: 1.25rem;
    outline: none;
}

.create-button {
    background: #335d8d;
    color:white;
    padding:0.5rem 1.5rem;
    border-radius: 3px;
    font-weight:600;
    cursor: pointer;
}

.create-button:hover {
    background: #20c8df;
}